.App-header {
    background-color: #111467; /* Set background color */
    height: 100px; /* Set header height */
    display: flex; /* Center content horizontally */

    align-items: center; /* Center content vertically */
    padding: 10px; /* Add some padding */
    color: white; /* Set text color to white for better contrast */
    font-weight: bold; /* Make the header text bold (optional) */
}
  
.header-container {
    display: flex; /* Use flexbox for logo and text arrangement */
    margin-left: 30px; /* Khoảng trống 30px bên trái */
}
  
.logo {
    /* Add your logo image here */
    width: 50px; /* Adjust logo width as needed */
    height: 50px; /* Adjust logo height as needed */
    margin-right: 10px; /* Add margin on the right */
}
  
.header-text {
    font-size: 20px; /* Adjust font size as needed */
}

.nav-links {
    padding: 10px 20px; /* Khoảng trống bên trong khung */
    border-radius: 5px; /* Bán kính bo tròn góc */
    position: absolute; /* Đặt vị trí tuyệt đối */
    right: 30px; /* Canh chỉnh theo chiều ngang */
}
  
.nav-links a {
    padding: 10px 15px;
    margin-right: 10px;
    text-decoration: none;
    color: white;
}
  
.nav-links a:hover {
    color: #FF0000; /* Chuyển màu chữ sang đỏ */
    text-decoration: underline; /* Thêm gạch chân */
}